<template>
  <div>
    <div class="item">
      <div class="tt">
        <div class="tname">小组讨论</div>
        <div class="btns">
          <el-button type="primary" size="small" @click="beforeShowAdd" :disabled="disabled">添加</el-button>
        </div>
      </div>
      <div class="table" v-loading="searchLoading">
        <common-table :columns="dataColumns" :datas="dataList" :pagger="false" th-class="table-innerhead" border
                      max-height="550">
          <template slot="CreateTime" slot-scope="scope">
            <span>{{scope.row['CreateTime']| formatDate(scope.row['CreateTime'])}}</span>
          </template>
          <template slot="oprate" slot-scope="scope">
            <el-button type="success" size="mini" @click="editData(scope.row)" :disabled="disabled">修改</el-button>
            <el-button type="danger" size="mini" @click="deleteData(scope.row)" :disabled="disabled">移除</el-button>
          </template>
        </common-table>
      </div>
    </div>

    <el-dialog title="编辑小组讨论"  :visible.sync="showEdit" width="600px" center>
      <el-form ref="form" :model="form" :rules="rules" size="small">
        <el-form-item prop="title">
          <el-input type="textarea" v-model="form.title" :autosize="{ minRows: 3, maxRows: 10}" placeholder="请输入小组讨论题目"
                    maxlength="500" :show-word-limit="true"></el-input>
        </el-form-item>
        <el-form-item style="text-align:center;">
          <el-button :loading="btnLoading" type="primary" @click="saveData('form')">保 存</el-button>
          <el-button @click="showEdit = false">取 消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>
<script>
  import list from "@/mixin/list";

  export default {
    mixins: [list],
    props: {
      courseId: {
        type: String,
        default: ""
      },
      disabled: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        showEdit: false,
        searchLoading: false,
        dataColumns: [
          {
            prop: "PGSContent",
            label: "题目"
            //   sort:true
          },
          {
            prop: "CreateTime",
            slot: true,
            label: "创建时间"
          },
          {
            prop: "oprate",
            label: "操作",
            slot: true,
            width: 220
          }
        ],
        dataList: [],
        form: {
          id: "",
          title: "",
          time: 0
        },
        rules: {
          title: {
            required: true,
            min: 1,
            max: 500,
            message: "长度在 1 到 500 个字符",
            trigger: "blur"
          },
          time: [
            { required: true, message: "试卷计时不能为空" },
            { type: "number", message: "试卷计时必须为数字值" }
          ]
        },
        btnLoading: false
      };
    },
    created() {
      this.searchData();
    },
    methods: {
      searchData() {
        this.searchLoading = true;
        this.dataList = [];
        this.$http
          .get(this.$api.GroupDiscussionList, { courseID: this.courseId })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.dataList.push(item);
              });
            }
            if (res.Result) {
              this.dataTotal = res.RowsCount || 0;
            }
            this.searchLoading = false;
          })
          .catch(err => {
            this.searchLoading = false;
          });
      },
      editData(row) {
        this.form.id = row["PGSID"];
        this.form.title = row["PGSContent"];
        this.showEdit = true;
      },
      saveData(formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.btnLoading = true;
            let reqData = {
              pgsID: this.form.id,
              courseID: this.courseId,
              pgsContent: this.form.title,
              pgsTime: this.form.time
            };
            this.$http
              .post(this.$api.GroupDiscussionAdd, reqData)
              .then(res => {
                if (res.Result) {
                  this.$message({
                    message: "保存成功！",
                    type: "success"
                  });
                  setTimeout(() => {
                    this.searchData();
                  }, 100);
                  this.showEdit = false;
                } else if (res.message) {
                  this.$message({
                    showClose: true,
                    ...res
                  });
                }
                this.btnLoading = false;
              })
              .catch(err => {
                this.btnLoading = false;
              });
          } else {
            this.$message({
              showClose: true,
              message: "请输入正确格式",
              type: "error"
            });
            return false;
          }
        });
      },
      deleteData(row) {
        this.$confirm("此操作将删除小组讨论, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$http
              .post(this.$api.GroupDiscussionDelete, { ids: row["PGSID"] })
              .then(res => {
                if (!res.Result) {
                  this.$message({
                    showClose: true,
                    ...res
                  });
                } else {
                  this.$message({
                    showClose: true,
                    message: "删除成功",
                    type: "success"
                  });
                  this.searchData();
                }
              })
              .catch(err => {
                this.$message({
                  showClose: true,
                  message: "操作失败",
                  type: "error"
                });
              });
          })
          .catch(err => {
          });
      },
      beforeShowAdd() {
        this.resetForm();
        this.showEdit = true;
      },
      resetForm() {
        this.form = {
          id: "",
          title: "",
          time: 0
        };
      }
    }
  };
</script>
<style lang="less" scoped>
</style>



