<template>
  <div class="item">
    <div class="tt">
      <div class="tname">课件</div>
      <div class="btns">
        <el-button type="primary" size="small" :disabled="disabled" @click="beforeShowAdd">添加</el-button>
      </div>
    </div>

    <div id="table" v-loading="searchLoading">
      <common-table :columns="dataColumns" :datas="dataList" :pagger="false" th-class="table-innerhead" border
                    max-height="550">
        <template slot="attribute" slot-scope="scope">
          <el-dropdown class="dropdown" @command="handleCommand($event,scope.row)">
            <span class="el-dropdown-link" v-if="scope.row.attribute == 1">课前<i
              class="el-icon-arrow-down el-icon--right"></i></span>
            <span class="el-dropdown-link" v-else-if="scope.row.attribute == 2">课中<i
              class="el-icon-arrow-down el-icon--right"></i></span>
            <span class="el-dropdown-link" v-else-if="scope.row.attribute == 3">课后<i
              class="el-icon-arrow-down el-icon--right"></i></span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item v-for="(item,index) in attributeOptions" :key="index" :command="item">{{item.label}}
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </template>
        <template slot="name" slot-scope="scope">
          <span>{{scope.row.name}}</span>
        </template>
        <template slot="size" slot-scope="scope">
          <span v-if="scope.row.size>1024*1024">{{Math.round( scope.row.size/1024.0/10.24)/100}}MB</span>
          <span v-else>{{Math.round(scope.row.size/10.24)/100}}KB</span>
        </template>
        <template slot="createTime" slot-scope="scope">
          <span>{{scope.row.createTime | formatDate(scope.row.createTime)}}</span>
        </template>
        <template slot="oprate" slot-scope="scope">
          <el-button type="danger" size="mini" @click="deleteData(scope.row)">移除</el-button>
          <!-- v-if="scope.row.attribute != 2"-->
          <el-button v-if="scope.row.attribute != 2" type="primary" size="mini" @click="studentTask(scope.row)">学生完成进度
          </el-button>
        </template>
      </common-table>
    </div>

    <el-dialog title="添加课件" :visible.sync="showEdit" width="800px" center class="dialog" @close="closeDialog">
      <div class="head_select">课件属性：
        <el-select v-model="selectAttribute" style="width:150px;" :disabled="disabled">
          <el-option v-for="(item, index) in attributeOptions" :key="index" :label="item.label"
                     v-model="item.value"></el-option>
        </el-select>
      </div>
      <div class="head_btn">
        <el-upload ref="upload" action :limit="1" :before-upload="beforeUpload">
          <el-button size="small" icon="el-icon-upload" type="primary">上传课件</el-button>
        </el-upload>
        <div style="font-size:12px;">单个文件大小不能超过500MB</div>
        <div style="text-align: right;">
          上传进度：
          <el-progress :text-inside="true" :stroke-width="13" :percentage="progress" status="success"
                       style="width: 120px;float: right;line-height: 20px;"></el-progress>
        </div>
      </div>

      <div v-loading="dataLoading">
        <el-transfer v-model="selectFiles" :data="fileDataList" filterable :props="propsOption"
                     :titles="['网盘文件', '备课文件']" @change="selectFileChange"></el-transfer>
      </div>

      <div class="foot_btn">
        <el-button size="small" @click="showEdit=false">关 闭</el-button>
      </div>
    </el-dialog>

  </div>
</template>
<script>
  import list from "@/mixin/list";

  export default {
    mixins: [list],
    props: {
      courseId: {
        type: String,
        default: ""
      },
      disabled: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        showEdit: false,
        searchLoading: false,
        dataColumns: [
          {
            prop: "attribute",
            label: "属性",
            slot: true,
            width: 100
          },
          {
            prop: "name",
            label: "名称",
            slot: true
          },
          {
            prop: "fileType",
            label: "文件类型",
            width: 80
          },
          {
            prop: "size",
            label: "大小",
            slot: true,
            width: 100
          },
          {
            prop: "createTime",
            label: "创建时间",
            slot: true,
            width: 100
          },
          {
            prop: "oprate",
            label: "操作",
            slot: true,
            width: 220
          }
        ],
        dataList: [],
        fileTypeOptions: [],
        fileType: "",
        selectFiles: [],
        preSelectFile: [], //之前选择的文件
        propsOption: {
          key: "materialId",
          label: "name"
        },
        progress: 0,
        attributeOptions: [
          { label: "课前", value: 1 },
          { label: "课中", value: 2 },
          { label: "课后", value: 3 },
        ],
        selectAttribute: 2,
        fileDataList: [],
        dataLoading: false,
        dialogChange: false //对话框是否有更新事件，此参数用来更新查询列表
      };
    },
    created() {
      this.searchData();
    },
    methods: {
      handleCommand(command, row) {
        this.$http
          .post(this.$api.CourseWareUpdateAttribute, {
            cid: row.id,
            attribute: command.value
          })
          .then(res => {
            if (!res.Result) {
              this.$message({
                showClose: true,
                message: "操作失败",
                type: "error"
              });
            } else {
              this.searchData();
            }
          })
          .catch(err => {
            this.$message({
              showClose: true,
              message: "操作失败",
              type: "error"
            });
          });
      },
      closeDialog() {
        // if (this.dialogChange) this.searchData();
      },
      searchData() {
        this.searchLoading = true;
        this.dataList = [];
        this.selectFiles = [];
        this.preSelectFile = [];

        this.$http
          .get(this.$api.CourseWareList, { courseID: this.courseId, attribute: -1 })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.dataList.push({
                  id: item.Key["CID"],
                  materialId: item.Key["MaterialID"],
                  attribute: item.Key["Attribute"],
                  createTime: item.Key["CreateTime"],
                  state: item.Key["State"],
                  name: item.Value["MaterialName"],
                  size: item.Value["MaterialFileSize"],
                  fileType: item.Value["MaterialFileExt"]
                });

                this.selectFiles.push(item.Key["MaterialID"]);
                this.preSelectFile.push(item.Key["MaterialID"]);
              });
            }
            if (res.Result) {
              this.dataTotal = res.RowsCount || 0;
            }
            this.searchLoading = false;
          })
          .catch(err => {
            this.searchLoading = false;
          });
      },
      /**
       * 获取资源类型(作废)
       */
      getDict() {
        this.dataLoading = true;
        this.fileTypeOptions = [];
        this.$http
          .get(this.$api.MaterialTypeList, { token: this.$http.getToken() })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.fileTypeOptions.push({
                  label: item["MTypename"],
                  value: item["MTypeID"],
                  code: item["MTCode"]
                });
              });
            }
            this.dataLoading = false;
          })
          .catch(err => {
            this.dataLoading = false;
          });
      },
      deleteData(row) {
        this.$confirm("此操作将删除课件, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.dataDelete([row.id], true);
          })
          .catch(err => {
          });
      },
      beforeShowAdd() {
        this.fileDataList = [];
        this.$http
          .get(this.$api.CourseMaterialList, { token: this.$http.getToken() })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.fileDataList.push({
                  name: item["MaterialName"],
                  materialId: item["MaterialID"],
                  size: item["MaterialFileSize"],
                  fileType: item["MaterialFileExt"]
                });
              });
            }
          })
          .catch(err => {
          });
        this.showEdit = true;
      },
      beforeUpload(file) {
        if (file.size > 1024 * 1024 * 500) {
          this.$message({
            showClose: true,
            message: "单个文件大小不能超过500MB",
            type: "error"
          });
          return false;
        }

        if (
          file.type.indexOf("cmd") > -1 ||
          file.type.indexOf("exe") > -1 ||
          file.type.indexOf("bat") > -1 ||
          file.type.indexOf("vbs") > -1
        ) {
          this.$message({
            showClose: true,
            message: "不支持的文件类型",
            type: "error"
          });
          return false;
        }

        let userInfo = this.$http.getUser();

        //这里是重点，将文件转化为formdata数据上传
        let fd = new FormData();
        fd.append("stream", file);
        fd.append("dirid", "");
        fd.append("Token", this.$http.getToken());
        fd.append("courseId", this.courseId);
        fd.append("username", userInfo.UserName);
        fd.append("attribute", this.selectAttribute);
        let that = this;
        that.progress = 0;
        let config = {
          onUploadProgress: progressEvent => {
            that.progress = (progressEvent.loaded * 100.0 / progressEvent.total | 0);
          }
        };
        this.$http
          .postFormDataAndProgress(this.$api.CourseWareUpload, fd, config)
          .then(res => {
            if (!res.Result) {
              this.$message({
                showClose: true,
                ...res
              });
            } else {
              this.$message({
                showClose: true,
                message: "上传成功",
                type: "success"
              });

              let material = {
                name: file.name,
                materialId: res.MaterialID,
                size: file.Size,
                fileType: file.type
              };
              this.dialogChange = true;
              this.fileDataList.push(material);
              this.searchData();
            }
          })
          .catch(err => {
            this.$message({
              showClose: true,
              message: "上传失败",
              type: "error"
            });
          });
        return false;
      },
      selectFileChange(key) {
        let listAdd = this.selectFiles.filter(
          r => this.preSelectFile.indexOf(r) === -1
        );
        let listDelete = this.preSelectFile.filter(
          r => this.selectFiles.indexOf(r) === -1
        );

        //是否增加
        if (listAdd.length > 0) {
          this.dataAdd(listAdd);
        } else if (listDelete.length > 0) {
          this.dataDelete(this.dataList.map(r => {
            if (listDelete.indexOf(r.materialId) > -1) return r.id;
          }));
        }
        this.dialogChange = true;
      },
      dataAdd(listId) {
        this.dataLoading = true;
        this.$http
          .post(this.$api.CourseWareAdd, {
            courseID: this.courseId,
            materialIDs: listId.join(","),
            attribute: this.selectAttribute
          })
          .then(res => {
            if (!res.Result) {
              this.$message({
                showClose: true,
                ...res
              });
              this.selectFiles = this.preSelectFile;
            } else {
              this.searchData();
              this.preSelectFile.push(listId);
            }
            this.dataLoading = false;
          })
          .catch(err => {
            this.$message({
              showClose: true,
              message: "操作失败",
              type: "error"
            });
            this.selectFiles = this.preSelectFile;
            this.dataLoading = false;
          });
      },
      dataDelete(listId, showSucess = false) {
        this.dataLoading = true;
        this.$http
          .post(this.$api.CourseWareDelete, { ids: listId.join(",") })
          .then(res => {
            if (!res.Result) {
              this.$message({
                showClose: true,
                ...res
              });
              this.selectFiles = this.preSelectFile;
            } else {
              this.searchData();
              if (showSucess) {
                this.$message({
                  showClose: true,
                  message: "删除成功",
                  type: "success"
                });
              }
            }
            this.dataLoading = false;
          })
          .catch(err => {
            this.$message({
              showClose: true,
              message: "操作失败",
              type: "error"
            });
            this.selectFiles = this.preSelectFile;
            this.dataLoading = false;
          });
      },
      studentTask(row) {
        sessionStorage.setItem("courseWare", JSON.stringify(row));
        sessionStorage.setItem("taskType", "CourseWare");
        this.$router.push("/main/teacherSpace/prepare/studentTask");
      },
    }
  };
</script>
<style lang="less" scoped>
  .dialog {
    .head_select {
      float: left;
    }

    .head_btn {
      text-align: right;
      margin-bottom: 20px;
    }

    /deep/ .el-transfer-panel {
      width: 326px;
    }

    .foot_btn {
      text-align: right;
      margin-top: 20px;
    }
  }
</style>



