<template>
  <div>
    <div class="item">
      <div class="tt">
        <div class="tname">随堂练习</div>
        <div class="btns">
          <el-button type="primary" size="small" @click="$router.push('/main/teacherSpace/practiceBank')">我的题库
          </el-button>
          <el-button type="primary" size="small" :disabled="disabled" @click="addData">练习添加</el-button>
        </div>
      </div>
      <div class="table" v-loading="searchLoading">
        <common-table :columns="dataColumns" :datas="dataList" :pagger="false" th-class="table-innerhead" border max-height="550">
          <template slot="attribute" slot-scope="scope">
            <el-dropdown class="dropdown" @command="handleCommand($event,scope.row)">
              <span class="el-dropdown-link" v-if="scope.row.attribute == 1">课前<i class="el-icon-arrow-down el-icon--right"></i></span>
              <span class="el-dropdown-link" v-else-if="scope.row.attribute == 2">课中<i class="el-icon-arrow-down el-icon--right"></i></span>
              <span class="el-dropdown-link" v-else-if="scope.row.attribute == 3">课后<i class="el-icon-arrow-down el-icon--right"></i></span>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item v-for="(item,index) in attributeOptions" :key="index" :command="item"  :disabled="disabled">{{item.label}}</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </template>
          <template slot="createTime" slot-scope="scope">
            <span>{{scope.row.createTime | formatDate(scope.row.createTime)}}</span>
          </template>
          <template slot="oprate" slot-scope="scope">
            <el-button type="success" size="mini" :disabled="disabled" @click="editData(scope.row)">修改</el-button>
            <el-button type="warning" size="mini" @click="previewData(scope.row)">预览</el-button>
            <el-button type="danger" size="mini" :disabled="disabled" @click="deleteData(scope.row)">移除</el-button>
            <el-button v-if="scope.row.attribute !== 2"  type="primary" size="mini" @click="studentTask(scope.row)">学生完成进度</el-button>
          </template>
        </common-table>
      </div>
    </div>

    <el-dialog :title="dataEdit?'随堂练习修改':'随堂练习创建'" :visible.sync="showEdit" width="600px" center @close="closeDialog">
      <!-- 编辑标题 -->
      <el-form ref="formTitle" :model="formTitle" :rules="rulesTitle" label-width="80px">
        <el-form-item label="试卷题库" prop="plid">
          <el-select v-model="formTitle.plid" placeholder="请选择试卷题库" style="width:100%;">
            <el-option v-for="(item, index) in practiceBankList" :key="index" :label="item.label"
                       v-model="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="练习属性" prop="attribute">
          <el-select v-model="formTitle.attribute" placeholder="请选择练习属性" style="width:100%;">
            <el-option v-for="(item, index) in attributeOptions" :key="index" :label="item.label"
                      v-model="item.value"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="答题计时" prop="time">
          <el-input-number v-model="formTitle.time" :min="1" :max="1000" label="试卷计时" controls-position="right"
                           :precision="0" :step="1"></el-input-number>
          分钟
        </el-form-item>
        <div style="text-align:right;">
          <el-button @click="showEdit = false">取消</el-button>
          <el-button :loading="btnLoading" type="primary" @click="saveFormTitle('formTitle')">
            保存
          </el-button>
        </div>
      </el-form>
    </el-dialog>

    <el-dialog title="随堂练习预览" :visible.sync="showPreview" width="800px" center @close="closePreviewDialog">
      <div class="pre_title">
        <span>题库名称：</span>
        <span>{{dataInfo.title}}</span>
      </div>
      <div class="pre_line">
        <span>试卷计时：</span>
        <span>{{dataInfo.time}}</span>
        <span>总题数：</span>
        <span>{{dataInfo.subjectList.length}}</span>
        <span>总分数：</span>
        <span>{{dataInfo.score}}</span>
      </div>

      <el-collapse v-model="dataInfo.subjectActive" class="collapse_main" accordion>
        <el-collapse-item v-for="(item, index) in dataInfo.subjectList" :key="index" :name="index">
          <template slot="title">
            <el-row :gutter="20" class="collapse_head">
              <el-col :span="24">
                <span>{{ (index+1)+'、['+ subjectTypeOptions[item.type-1].label +']'+ item.title}}</span>
              </el-col>
            </el-row>
          </template>
          <el-card class="collapse_card">
            <div v-if="[1, 2].indexOf(item.type) > -1">
              <h4>选项：</h4>
              <p v-for="(sub, k) in item.options" :key="k">{{sub.value +'、'+sub.label}}</p>
            </div>
            <h4>答案：</h4>
            <div class="card_line">
              <span>正确答案：</span>
              <span>{{item.type===2?item.multiAnswer.join(''):item.answer}}</span>
              <span>分数：</span>
              <span>{{item.score}}</span>
            </div>
            <h4>解析：</h4>
            <div class="card_content">{{item.analysis}}</div>
          </el-card>
        </el-collapse-item>
      </el-collapse>
      <div style="text-align:right;margin-top:15px;">
        <el-button size="small" @click="showPreview=false">关 闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import list from "@/mixin/list";

  export default {
    mixins: [list],
    props: {
      courseId: {
        type: String,
        default: ""
      },
      disabled: {
        type: Boolean,
        default: false
      }
    },
    data() {
      return {
        showEdit: false,
        editTitle: true, //默认编辑标题
        showPreview: false,
        searchLoading: false,
        dataColumns: [
          {
            prop: "attribute",
            label: "属性",
            slot: true,
            width: 100
          },
          {
            prop: "title",
            label: "题库名称"
          },
          {
            prop: "time",
            label: "答题计时(分)",
            width: 120
          },
          {
            prop: "createTime",
            label: "创建时间",
            slot: true,
            width: 100
          },
          {
            prop: "oprate",
            label: "操作",
            slot: true,
            width: 350
          }
        ],
        dataList: [],
        practiceBankList: [],
        formTitle: {
          id: "",
          plid: "",
          attribute: "",
          time: 0
        },
        rulesTitle: {
          plid: {
            required: true,
            message: "请选择试卷题库",
            trigger: "change"
          },
          attribute: {
            required: true,
            message: "请选择练习属性",
            trigger: "change"
          },
          time: [
            { required: true, message: "试卷计时不能为空" },
            { type: "number", message: "试卷计时必须为数字值" }
          ]
        },
        attributeOptions: [
          { label: "课前", value: 1 },
          { label: "课中", value: 2 },
          { label: "课后", value: 3 },
        ],
        subjectTypeOptions: [
          { label: "单选题", value: 1 },
          { label: "多选题", value: 2 },
          { label: "判断题", value: 3 },
          { label: "问答题", value: 4 }
        ],
        singleOptions: ["A", "B", "C", "D", "E", "F", "G", "H"],
        judgeOptions: ["对", "错"],
        btnLoading: false,
        dialogChange: false, //对话框是否有更新事件，此参数用来更新查询列表
        dataEdit: false, //是否修改试卷
        dataInfo: {
          id: "",
          title: "",
          time: 0,
          score: 0,
          subjectActive: 0,
          subjectList: []
        }
      };
    },
    created() {
      this.searchData();
    },
    methods: {
      handleCommand(command, row) {
        let reqData = {
          cpID: row.id,
          courseID: this.courseId,
          plID: row.plid,
          attribute: command.value,
          cpTime: row.time
        };

        this.$http
          .post(this.$api.ClassPractiseAdd, reqData)
          .then(res => {
            if (!res.Result) {
              this.$message({
                showClose: true,
                message: "操作失败",
                type: "error"
              });
            } else {
              this.searchData();
            }
          })
          .catch(err => {
            this.$message({
              showClose: true,
              message: "操作失败",
              type: "error"
            });
          });
      },
      closeDialog() {
        if (this.dialogChange) this.searchData();
      },
      searchData() {
        this.searchLoading = true;
        this.dataList = [];
        this.$http
          .get(this.$api.ClassPractiseList, { courseID: this.courseId })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.dataList.push({
                  id: item.Datas["CPID"] || "",
                  plid: item.Datas["PLID"] || "",
                  attribute: item.Datas["Attribute"],
                  time: item.Datas["CPTime"] || "",
                  state: item.Datas["State"] || 0,
                  createTime: item.Datas["CreateTime"] || "",
                  title: item["Title"] || ""
                });
              });
            }
            if (res.Result) {
              this.dataTotal = res.RowsCount || 0;
            }
            this.searchLoading = false;
          })
          .catch(err => {
            this.searchLoading = false;
          });
      },
      resetFormTitle(form) {
        this.formTitle = {
          id: form ? form.id : "",
          plid: form ? form.plid : "",
          attribute: form ? form.attribute : "",
          time: form ? form.time : 0
        };
      },
      addData() {
        this.dataEdit = false;
        this.resetFormTitle();
        this.getPracticeBankList();
        this.showEdit = true;
      },
      editData(row) {
        this.dataEdit = true;
        this.resetFormTitle(row);
        this.getPracticeBankList();
        this.showEdit = true;
      },
      /**
       * 保存标题
       */
      saveFormTitle(formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            this.btnLoading = true;
            let reqData = {
              cpID: this.formTitle.id,
              courseID: this.courseId,
              plID: this.formTitle.plid,
              attribute: this.formTitle.attribute,
              cpTime: this.formTitle.time
            };
            this.$http
              .post(this.$api.ClassPractiseAdd, reqData)
              .then(res => {
                if (res.Result) {
                  this.dialogChange = true;
                  this.showEdit = false;
                  this.$message({
                    message: this.formTitle.id ? "修改成功！" : "创建成功！",
                    type: "success"
                  });
                } else if (res.message) {
                  this.$message({
                    showClose: true,
                    ...res
                  });
                }
                this.btnLoading = false;
              })
              .catch(err => {
                this.btnLoading = false;
              });
          } else {
            this.$message({
              showClose: true,
              message: "请输入正确格式",
              type: "error"
            });
            return false;
          }
        });
      },

      previewData(row) {
        this.getData(row);
        this.showPreview = true;
      },
      getData(row) {
        this.dataInfo.subjectList = [];
        this.dataInfo.title = row.title || "";
        this.dataInfo.time = row.time || 0;
        this.dataInfo.score = 0;

        this.$http
          .get(this.$api.ClassPractiseSubjectList, { plid: row.plid })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.dataInfo.subjectList.push({
                  plid: item.Key["PLID"],
                  id: item.Key["PSID"],
                  no: item.Key["PSSerial"] || 0,
                  title: item.Key["PSContent"],
                  type: item.Key["PSType"],
                  score: item.Key["PSScore"] || 0,
                  analysis: item.Key["PSanalysis"],
                  answer: item.Key["PSAnswer"],
                  multiAnswer:
                    item.Key["PSType"] !== 2
                      ? []
                      : item.Key["PSAnswer"].split(""),
                  options:
                    item.Key["PSType"] === 4
                      ? []
                      : item.Value.map(v => {
                        return { label: v["SOContent"], value: v["SOSerial"] };
                      })
                });
                this.dataInfo.score += item.Key["PSScore"] || 0;
              });
            }
          })
          .catch(err => {
            // this.searchLoading = false;
          });
      },

      deleteData(row) {
        this.$confirm("此操作将删除随堂练习, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$http
              .post(this.$api.ClassPractiseDelete, { ids: row.id })
              .then(res => {
                if (!res.Result) {
                  this.$message({
                    showClose: true,
                    ...res
                  });
                } else {
                  this.$message({
                    showClose: true,
                    message: "删除成功",
                    type: "success"
                  });
                  this.searchData();
                }
              })
              .catch(err => {
                this.$message({
                  showClose: true,
                  message: "操作失败",
                  type: "error"
                });
              });
          })
          .catch(err => {
          });
      },

      getPracticeBankList() {
        this.practiceBankList = [];
        this.$http
          .get(this.$api.PracticeBankList, { token: this.$http.getToken() })
          .then(res => {
            if (res.Result && res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(item => {
                this.practiceBankList.push({
                  label: item["PLTitle"] || "",
                  value: item["PLID"] || ""
                });
              });
            }
          })
          .catch(err => {
            this.searchLoading = false;
          });
      },

      // 关闭编辑和预览窗体
      closePreviewDialog() {
        this.dataEdit = false;
      },
      studentTask(row) {
        sessionStorage.setItem("classPractice", JSON.stringify(row));
        sessionStorage.setItem("taskType", "ClassPractice");
        this.$router.push("/main/teacherSpace/prepare/studentTask");
      },
    }
  };
</script>
<style lang="less" scoped>
  .dialog_title {
    font-size: 16px;
    text-align: center;
    margin-bottom: 15px;
  }

  .pre_title {
    font-size: 16px;
    line-height: 2;

    .title_btn {
      display: inline-block;
      padding-left: 30px;
    }
  }

  .pre_line {
    font-size: 14px;
    line-height: 2;

    span:nth-child(2n) {
      padding-right: 30px;
    }
  }

  /deep/ .el-collapse-item__header {
    min-height: 48px;
    height: unset;
    line-height: 1.2;
  }

  .collapse_main {
    margin-top: 20px;

    .collapse_head {
      width: 100%;

      span {
        font-size: 15px;
        line-height: 1.5;
      }
    }

    .collapse_card {
      font-size: 14px;

      h4 {
        font-size: 16px;
        border-left: 5px solid #59d8ff;
        padding-left: 10px;
      }

      p {
        font-size: 14px;
        line-height: 1.5;
        margin-bottom: 0;
        margin-left: 20px;
      }

      .card_line {
        margin-left: 20px;
        line-height: 2;

        span:nth-child(2n) {
          color: #f56c6c;
          background: #f8f8f8;
          padding: 0 5px;
          margin-right: 30px;
        }
      }

      .card_content {
        margin-left: 20px;
        padding: 3px 10px;
        color: #f56c6c;
        background: #f8f8f8;
      }
    }
  }
</style>



