<template>
  <div id="lesson-info">
    <div class="top">
      <div class="tt">课程信息</div>
      <div class="lname">课程：{{course['CourseName']}}
        <span v-if="course['State']===0" style="color: #67c23a;">(准备中)</span>
        <span v-else-if="course['State']===1" style="color: #409eff;">(上课中)</span>
        <span v-else style="color: #f56c6c;">(已结束)</span>
      </div>
      <div class="info">
        <div class="tc">
          <i class="iconfont icon-jiaoshi"></i>
          老师：{{course['TeacherName']}}
        </div>
        <div class="cs">
          <i class="iconfont icon-jiaoshianpai"></i>
          教室：{{course['ClassroomName']}}
        </div>
        <div class="gd">
          <i class="iconfont icon-banji2"></i>
          班级：{{course['ClassName']}}
        </div>
        <div class="ltime">
          <i class="iconfont icon-shizhong3"></i>
          上课时间：{{formatDate(course['ClassDate'], 'yyyy年MM月dd日') + ' ' + formatDate(course['StartTime'], 'hh:mm') + '-' + formatDate(course['EndTime'], 'hh:mm') }}
        </div>
      </div>
    </div>

    <div class="main">
      <div class="tt">备课流程</div>
      <div class="tabs">
          <div class="content">
            <course-ware :course-id="courseID" :disabled="false"></course-ware>
            <class-practice :course-id="courseID" :disabled="false"></class-practice>
            <discussion :course-id="courseID" :disabled="false"></discussion>
          </div>
      </div>
    </div>
  </div>
</template>

<script>
import CourseWare from "./courseTeaching/CourseWare";
import ClassPractice from "./courseTeaching/ClassPractice";
import Discussion from "./courseTeaching/Discussion";
import { formatDate } from "@/utils/utils";
export default {
  name: "",
  components: { CourseWare, ClassPractice, Discussion },
  props: {},
  data() {
    return {
      course: JSON.parse(sessionStorage.getItem("course")),
      courseID: sessionStorage.getItem("courseID"),
      activeName: "second",
    };
  },
  beforeCreate() {
    if (!sessionStorage.getItem("course"))
      this.$router.push("/main/teacherSpace/prepare/courseView");
  },
  created() {},
  methods: {
    formatDate(dateValue, format) {
      return formatDate(dateValue, format);
    },
    handleClick(tab, event) {
      console.log(tab, event);
    }
  },
  computed: {},
  watch: {}
};
</script>

<style lang='less'>
#lesson-info {
  text-align: left;
  .top {
    width: 955px;
    background: rgba(255, 255, 255, 1);
    border-radius: 3px;
    padding: 20px;
    font-size: 14px;
    color: #333;
    margin-bottom: 20px;
    overflow: hidden;

    .tt {
      margin-bottom: 27px;
    }
    .lname {
      margin-bottom: 25px;
    }
    .info {
      font-size: 12px;
      display: flex;
      justify-content: space-between;

      i {
        margin-right: 10px;
        color: #2a9efc;
      }
    }
  }

  .main {
    width: 956px;
    // height: 1265px;
    background: rgba(255, 255, 255, 1);
    border-radius: 0px;
    padding: 20px;
    .tabs {
      margin-top: 30px;
    }
    .content {
      .item {
        .tt {
          // overflow: hidden;
          color: #333;
          font-size: 17px;
          margin-bottom: 40px;
          height: 32px;
          line-height: 32px;
          border-left: 3px solid #2a9efc;
          padding-left: 10px;
          display: flex;
          justify-content: space-between;
          margin-bottom: 20px;
          position: relative;
          .btns {
            position: absolute;
            right: 2px;
          }
        }
      }

      .el-table {
        color: #999;
        margin-bottom: 30px;
      }
      .el-dialog__header {
        text-align: left;
        border-bottom: 1px solid #f2f2f2;
      }

      .yl-dg {
        .el-dialog {
          width: 617px;
          height: 590px;
          background: rgba(255, 255, 255, 1);
          .el-dialog__title {
            font-size: 14px;
            color: #333;
          }
          .el-dialog__body {
            padding-bottom: 20px;
          }
          .ct {
            .item {
              margin-bottom: 30px;
              .tt {
                border-left: none;
                padding-left: 0px;
              }
              .selects {
                padding-left: 10px;
                .se {
                  margin-bottom: 5px;
                  font-size: 12px;
                  color: #333;
                }
              }
              .da {
                margin-bottom: 15px;
                span {
                  color: #fb2222;
                }
              }
            }
          }
        }
      }
    }
  }
}
</style>



